.home-main {
  padding: 20px;
}




/* Layout chá»©a slideshow vÃ  danh sÃ¡ch tin */
/* Desktop */
.content-wrapper {
  display: flex;
  gap: 20px;
}


/* Slideshow */
.slideshow {
   display: flex;
  position: relative;
  width: 40%;
  height: 350px;
  justify-content: center;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.slideshow-img {
   max-width: 100%;
  height: auto;   /* giá»¯ Ä‘Ãºng tá»‰ lá»‡ gá»‘c */
  display: block;
  object-fit: cover;
  
}


.btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 18px;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

/* Danh sÃ¡ch tin tá»©c */
.news-list {
  width: 60%;
  height: 320px;
  overflow-y: scroll;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.news-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.news-list li {
  padding: 10px 0;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: 0.3s;
}

.news-list a {
  text-decoration: none;
  color: #333; /* chá»¯ Ä‘en máº·c Ä‘á»‹nh */
}

.news-list a:hover {
  color: #007bff; /* xanh khi hover */
}




/* Layout 3 cá»™t cho GÃ³c nhÃ¬n / PhÃ³ng sá»± / NhÃ¢n sá»± má»›i */
.news-sections {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 30px;
}

/* Block */
.news-block {
  border-top: 3px solid #e6007e; /* gáº¡ch mÃ u */
  padding-top: 10px;
}

/* TiÃªu Ä‘á» */
.section-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* áº¢nh chÃ­nh */
.news-img {
  width: 320px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

/* BÃ i viáº¿t chÃ­nh */
.main-article {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 8px;
  color: #000;
}


.main-article a {
  color: #333;          /* Ã©p mÃ u máº·c Ä‘á»‹nh */
  text-decoration: none;
}

.main-article:hover a {
  color: #007bff;
}

/* BÃ i phá»¥ */
.sub-articles {
  list-style: none;
  padding: 0;
  margin: 0;
  
}
 

.sub-articles li {
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 1.4; /* dá»… Ä‘á»c hÆ¡n */
  color: #333;      /* giá»¯ mÃ u xÃ¡m Ä‘áº­m */
  cursor: pointer;
}

/* Náº¿u cÃ³ link trong bÃ i phá»¥ */
.sub-articles li a {
  color: #333;          /* Ã©p mÃ u máº·c Ä‘á»‹nh */
  text-decoration: none;
}

.sub-articles li:hover a {
  color: #007bff;
}



/* tÃ¬m kiáº¿m */
/* Banner */
#banner {
  position: relative; /* Ä‘á»ƒ con absolute bÃ¡m theo banner */
  text-align: center;
  margin-bottom: 40px;
}

.banner-image {
  width: 100%;
  border-radius: 10px;
}





.title_DT {
  text-align: center;
  background: linear-gradient(90deg, #ff5fa2, #ff7bbf, #ff99cc);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;       /* Ä‘á»ƒ lá»™ gradient */
  font-weight: 800;
  margin: 16px 0;
}


/* Mobile nhá» hÆ¡n 768px */
@media (max-width: 768px) {
  .content-wrapper {
    flex-direction: column; /* xáº¿p dá»c */
  }
.slideshow, .news-list {
    width: 100%; /* chiáº¿m háº¿t chiá»u ngang */
  }

  .news-sections {
    grid-template-columns: 1fr; /* má»—i block 1 hÃ ng */
  }

  .news-img {
    width: 100%;
    height: auto;
  }
}
