.image-slider {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  
}

.image-item img {
  width: 220px;
   height: auto;   /* giá»¯ Ä‘Ãºng tá»‰ lá»‡ gá»‘c */
  display: block;
  object-fit: cover;
  border-radius: 8px;
  transition: opacity 0.5s ease-in-out;
}


/* --------- Desktop lá»›n: tá»« 1080px trá»Ÿ lÃªn --------- */
@media (min-width: 1080px) {
  .image-slider {
    justify-content: center;
    gap: 20px;
  }

  .image-item img {
    width: 200px;    /* to hÆ¡n trÃªn desktop */
    max-width: 220px;
  }
}

/* --------- Tablet & Mobile vá»«a: dÆ°á»›i 768px --------- */
@media (max-width: 768px) {
  .image-slider {
    flex-wrap: wrap;        
    justify-content: center;
    gap: 15px;
  }

  .image-item img {
    width: 60%;     /* má»—i hÃ ng 2 áº£nh */
    max-width: 200px;
  }
}

/* --------- Mobile nhá»: dÆ°á»›i 480px --------- */
@media (max-width: 480px) {
  .image-slider {
    flex-direction: column;  /* xáº¿p dá»c */
    align-items: center;
    gap: 10px;
  }

  .image-item img {
    width: 70%;    /* 1 áº£nh má»—i hÃ ng */
    max-width: 300px;
  }
}


